import menuItems from './menu.json';
import './styles.css';
import menuItemTemplate from './menu-item.hbs'

const refs = {
  menuList: document.querySelector('.js-menu'),
  switchTheme: document.querySelector('.js-switch-input'),
  currentTheme: document.querySelector('body'),
};

const persistedTheme = localStorage.getItem('Theme');

if (persistedTheme) {
  refs.currentTheme.classList.add(persistedTheme);
}

if (persistedTheme === 'dark-theme') {
  refs.switchTheme.checked = true;
}

refs.switchTheme.addEventListener('change', handleSwitchTheme);

function handleSwitchTheme(event) {
  if (!refs.currentTheme.classList.contains('dark-theme')) {
    refs.currentTheme.classList.remove('light-theme');
    refs.currentTheme.classList.add('dark-theme');
  } else {
    refs.currentTheme.classList.remove('dark-theme');
    refs.currentTheme.classList.add('light-theme');
  };
  localStorage.setItem('Theme', refs.currentTheme.classList);
};

function buildMenuList(menuItems) {
  const markup = menuItems.map(menuItem => menuItemTemplate(menuItem)).join('');
  refs.menuList.insertAdjacentHTML('beforeend', markup);
}

buildMenuList(menuItems);
